import React, { useEffect } from 'react';
import { Box, Grid } from '@material-ui/core'
import Typography from '@material-ui/core/Typography'
import { useSelector } from 'react-redux';
import SkillCard from './card';
// image logo
import ReactLogo from '../../../ASSETS/img/reactjs-logo.png';
import PostmanLogo from '../../../ASSETS/img/postman.png';
import HtmlLogo from '../../../ASSETS/img/html.png';
import CssLogo from '../../../ASSETS/img/css.png';
import JsLogo from '../../../ASSETS/img/js.png';



export default (props: any) => {

  const redux = useSelector((state: any) => state.redux);
  const [attr, setAttr] = React.useState({
    fontSize: 28,
    fs: 26,
    fs1: 14,
    fs2: 33,
    fs3: 16,
    align: 'i-y-top',
    padding: 140
  });

  useEffect(() => {
    redux.app.current_grid != 'xs' ? setAttr({
      fontSize: 28,
      fs: 26,
      fs1: 14,
      fs2: 33,
      fs3: 16,
      align: 'i-y-top',
      padding: 140
    }) :
      setAttr({
        fontSize: 20,
        fs: 20,
        fs1: 14,
        fs2: 33,
        fs3: 16,
        align: 'i-y-center',
        padding: 20
      });

    console.log('Grid and font change!');
  }, [redux.app.current_grid]);

  const list = [{
    name: 'Reactjs',
    img: ReactLogo
  },
  {
    name: 'Postman',
    img: PostmanLogo
  },
  {
    name: 'HTML',
    img: HtmlLogo
  },
  {
    name: 'CSS',
    img: CssLogo
  },
  {
    name: 'Javascript',
    img: JsLogo
  }];

  return (
    <Box id='skills-section' component='span' className='d-100 m-100 t-100' style={{ padding: attr.padding + 'px' }}>
      <div style={{ marginBottom: '40px' }}>
        <Typography style={{ fontSize: attr.fs1, fontWeight: 0 }}><b>Skills</b></Typography>
        <Typography variant="h2" style={{ fontSize: attr.fs2, fontWeight: 600 }}>My <b>skills and tools</b></Typography>
      </div>
      <Grid container spacing={2} className='d-100 m-100 t-100'>
        <SkillCard list={list} />
      </Grid>
    </Box>
  )
}